.App{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.turn{
    font-size: 7vh;
}

.board{
    display: grid;
    grid-template-columns: repeat(3, 70px);
    grid-template-rows: repeat(3, 70px);
}

.segment{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border: 1px black solid;
    width: 70px;
    font-size: 5vh;
    user-select: none;
}

.turn{
    color: #fff;
}

h1 {
    color:white;
}